import React,{Component} from 'react';


import {HashRouter, Link, Switch, Route} from "react-router-dom";
import './App.scss';
import Navigations from '../Navigations'
import EntryForm from "../Layout/FormEntry"
import NowWheel from '../Layout/NowWheel'
import FutureWheel from '../Layout/FutureWheel'
import './App.scss'



const NotFound =()=> <h2>404 strony nie znaleziono</h2>;

function App() {
  return (
      <HashRouter>
        <Navigations />
        <Switch>
          <Route exact path ='/' component = {NowWheel}/>


          <Route path = '/future' component = {FutureWheel}/>
          <Route path = '/send' component = {EntryForm}/>
            <Route path = '/service' component = {Service}/>
            <Route component ={NotFound}/>



        </Switch>



      </HashRouter>)

}

export default App;
class Service extends Component{
    state = {
        questionairStatus: 0,
        seriesForNow: [],
        seriesForFuture: [],
        userContactData: {}
    }
    hadleClick = ()=>{
        this.setState({
            questionairStatus: this.state.questionairStatus +1
        })
    }
    hadleSendMail = ()=>{
        console.log("Wysłałbym state",this.state)
    }
    render(){
        const stage = this.state.questionairStatus
        let content;
        if(stage===0){
            content = <h1>Tu będzie formularz z danych </h1>
        }else if(stage ===1){
            content =  <h1>Wheel for now </h1>

        }else if(stage ===2){
            content =  <h1>Wheel for future </h1>
        }else{
            content = <button onClick={this.hadleSendMail}>Wyślij</button>
        }

        return (
            <>
                {content}
                <button onClick={this.hadleClick} >Dalej</button>
            </>
        )
    }
}